const desc = `
  # Button
  
`;

const base = `
  \`\`\`jsx
    import { Button } from 'react-stone';

    ReactDOM.render(
      <div>
        <Button>Button</Button>
      </div>
      , mountNode
    );
  \`\`\`
`;

const size = `
  \`\`\`jsx
    import { Button } from 'react-stone';

    ReactDOM.render(
      <div>
        <Button size='small'>Button small</Button>
        <Button>Button base</Button>
        <Button size='large'>Button large</Button>
      </div>
      , mountNode
    );
  \`\`\`
`;

const disabled = `
  \`\`\`jsx
    import { Button } from 'react-stone';

    ReactDOM.render(
      <div>
        <Button disabled={true}>Button Disabled</Button>
      </div>
      , mountNode
    );
  \`\`\`
`;

const theme = `
  \`\`\`jsx
    import { Button } from 'react-stone';

    ReactDOM.render(
      <div>
        <Button theme='blue'>Button theme blue</Button>
        <Button theme='gray'>Button theme gray</Button>
      </div>
      , mountNode
    );
  \`\`\`
`;

const type = `
  \`\`\`jsx
    import { Button } from 'react-stone';

    ReactDOM.render(
      <div>
        <Button htmlType='submit'>Button Submit</Button>
      </div>
      , mountNode
    );
  \`\`\`
`;


const icon = `
  \`\`\`jsx
    import { Button } from 'react-stone';

    ReactDOM.render(
      <div>
        <Button icon="delete">delect</Button>
        <Button icon="upload">upload</Button>
        <Button icon="girl">girl</Button>
        <Button icon="open">open</Button>
        <Button icon="products">products</Button>
        <Button icon="data">data</Button>
        <Button icon="success">success</Button>
        <Button icon="wait">wait</Button>
      </div>
      , mountNode
    );
  \`\`\`
`;

const radius = `
  \`\`\`jsx
    import { Button } from 'react-stone';

    ReactDOM.render(
      <div>
        <Button radius='small'>Button radius small</Button>
        <Button radius='half'>Button radius half</Button>
        <Button radius='circle' icon="delete" />
      </div>
      , mountNode
    );
  \`\`\`
`;

const api = `
  ## Options

     |       |          |     
  ----    | ----    | ----    | ----    |
  size  | String  | 'base' |   'small', 'base', 'large'
  icon  | String  |   |  
  radius | String | 'small' |   'small','half','circle'
  disabled | Boolean | false | 
  htmlType | String | 'button' |  button  type  [HTML ](https://developer.mozilla.org/zh-CN/docs/Web/HTML/Element/button)
  theme | String | 'blue' | 'blue','gray'

  ## Events
     |        |        |
  ----    | ----      | ----        |
  onClick | click  handler | event
`;

export {
  desc,
  base,
  size,
  disabled,
  theme,
  type,
  icon,
  radius,
  api,
};



