import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import Input from '../../../components/Input/Input';
import Icon from '../../../components/Icon/Icon';
import * as demo from './docs';
import DemoWrap from '../../components/DemoWrap/DemoWrap';
import './page.scss';

class ButtonPage extends Component {
  render() {
    return (
      <div className="markdown">
        <ReactMarkdown source={demo.desc} />
        <h2></h2>
        <DemoWrap desc="" code={demo.base}>
          <div className='demo-input-wrap'>
            <Input />
          </div>
          <div className='demo-input-wrap'>
            <Input placeholder="placeholder" />
          </div>
        </DemoWrap>
        <DemoWrap desc="" code={demo.size}>
          <div className='demo-input-wrap'>
            <Input size="small" placeholder="small size"/>
          </div>
          <div className='demo-input-wrap'>
            <Input size="base" placeholder="base size"/>
          </div>
          <div className='demo-input-wrap'>
            <Input size="large" placeholder="large size"/>
          </div>
        </DemoWrap>
        <DemoWrap desc="disabled" code={demo.disabled}>
          <div className='demo-input-wrap'>
            <Input disabled={true} placeholder="" />
          </div>
        </DemoWrap>
        <DemoWrap desc="value, input" code={demo.value}>
          <div className='demo-input-wrap'>
            <Input value={ 123 }/>
          </div>
        </DemoWrap>
        <DemoWrap desc="typeinput" code={demo.type}>
          <div className='demo-input-wrap'>
            <Input type="password" placeholder="typepassword"/>
          </div>
        </DemoWrap>
        <DemoWrap desc="addon" code={demo.addon}>
          <div className='demo-input-wrap'>
            <Input addonBefore="http://" addonAfter=".com" />
          </div>
          <div className='demo-input-wrap'>
            <Input addonBefore="" addonAfter={<Icon type='search' />} />
          </div>
        </DemoWrap>
        <DemoWrap desc="radiusinput" code={demo.radius}>
          <div className='demo-input-wrap'>
            <Input radius="radius" placeholder=""/>
          </div>
        </DemoWrap>
        <DemoWrap desc="maxlength, " code={demo.maxlength}>
          <div className='demo-input-wrap'>
            <Input maxLength={5} placeholder="" />
          </div>
        </DemoWrap>
        <ReactMarkdown source={demo.api} />
      </div>
    );
  }
}

export default ButtonPage;



